import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {BiblePage} from './bible.page';

const routes: Routes = [
    {
        path: '',
        component: BiblePage,
        children: [
            {
                path: 'old-testament',
                children: [
                    {
                        path: '',
                        loadChildren: () => import('../old-testament/old-testament.module').then(m => m.OldTestamentPageModule)
                    }
                ]
            },

            {
                path: 'new-testament',
                children: [
                    {
                        path: '',
                        loadChildren: () =>
                            import('../new-testament/new-testament.module').then(m => m.NewTestamentPageModule)
                    }
                ]
            },
            {
                path: '',
                redirectTo: '/bible/new-testament',
                pathMatch: 'full'
            }
        ]
    },
    {
        path: '',
        redirectTo: '/bible/new-testament',
        pathMatch: 'full'
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class BibleTabsRouterModule {
}
